<script>
	import Panel from './Panel.svelte';
	import Map from './Map.svelte';
	import MapSource from './MapSource.svelte';
	import MapLayer from './MapLayer.svelte';
	import { point, distance } from '@turf/turf';
	import { getData, getBoundary, getState, setState, shuffle, withinPolygon, sleep } from './utils.js';
	
	const mapstyle = 'https://raw.githubusercontent.com/bothness/ons-basemaps/master/data/style-outdoor.json';
	const lsoabounds = {
		url: 'https://cdn.ons.gov.uk/maptiles/administrative/lsoa/v2/boundaries/{z}/{x}/{y}.pbf',
		layer: 'lsoa',
		code: 'areacd'
	};
	const centroids = './data/lsoa-centroids.csv';
	const baseurl = 'https://ahmadb8.sg-host.com/lsoa/';
	const postcodesurl = 'https://api.postcodes.io/postcodes/';
	const typesFilter = ['City', 'Town'];
	const namesFilter = ['Play Space', 'Other Sports Facility', 'Bowling Green', 'Tennis Court', ];

	// Data
	let map = null;
	let lookup;
	let boundary;
	
	// State
	let game = {
		started: false,
		place: null,
		code: null,
		hovered: null,
		options: null,
		message: null,
		entry: null,
		postcode: null,
		history: [],
		named: [],
		order: []
	}

	// Get the LSOA for a postcode
	function getPostcode(postcode) {
		game.postcode = null;

		fetch(postcodesurl + postcode)
		.then(res => res.json())
		.then(json => {
			game.code = json.result.codes.lsoa;
		});
	}
	
	// Start game in random place
	function startGame() {
		let codes = Object.keys(lookup);
		codes = codes.filter(code => !game.named.includes(code));
		// select random code
		game.place = lookup[codes[Math.floor(Math.random() * codes.length)]];
		game.order = byDistance(game.place.code);
		doTurn();
	}

	// Sort all unnamed LSOAs by distance from the selected one
	function byDistance(code) {
		let codes = Object.keys(lookup);

		// Filter for codes of unnamed areas
		codes = codes.filter(cd => !game.named.includes(cd));

		// Calculate the distance of each LSOA from the selected one
		let pt = point([lookup[code].lng, lookup[code].lat]);
		let array = [];
		codes.forEach(cd => { array.push({ code: cd, distance: distance(pt, point([lookup[cd].lng, lookup[cd].lat])) }) });

		// Sort LSOAs by distance and return codes in order
		array.sort((a, b) => a.distance - b.distance);
		return array.map(d => d.code);
	}

	// Post name to CSV
	function addName(code, name) {
		let cd = code.replace(/"/g, "");
    let nm = name.replace(/"/g, "");
    let entry = btoa('"' + cd + '","' + nm + '"');
		let str = Date.now(); // Timestring, to avoid URL caching

		fetch(baseurl + 'api.php?entry=' + entry + '&str=' + str)
		.then(res => {
			if (res.ok) {
        console.log('Success!');
				if (!game.history.includes(code)) { game.history.push(code) };
				if (!game.named.includes(code)) { game.named.push(code) };
				game.entry = null;
				game.options = null;
				game.message = `Thank you!`;
				setState('history', game.history);
      } else {
        console.log('Fail!');
      }
    });
	}

	// Select the next place in the order
	function nextPlace() {
		let index = game.order.indexOf(game.code) + 1;
		game.place = lookup[game.order[index]];
		doTurn();
	}

	// New turn. Get boundary. Get names. Get features.
	function doTurn() {
		game.message = null;
		
		if (!game.started) { game.started = true };
		
		// Update code to match place code (for highlighting on map)
		if (game.code != game.place.code) { game.code = game.place.code } ;
		
		// Get geographic boundary of selected place
		getBoundary(game.place.code)
		.then(result => {
			boundary = result;
			
			// Fit map to boundary
			map.fitBounds(result.bounds, { padding: 20, animate: false });
			
			sleep(2000).then(() => getFeatures());
		});
	}
	
	// 
	function getFeatures() {
		if (boundary) {
			let options;

			if (game.place.names[0]) {
				let arr = [];
				game.place.names.forEach(name => {
					if (!arr.includes(name)) { arr.push(name) };
				});
				options = arr;
			} else {
				let names = map.querySourceFeatures('composite', { sourceLayer: 'names' });
				names = names.filter(name => name.properties.name1);
				names = names.filter(name => !typesFilter.includes(name.properties.type));
				names = names.filter(name => !namesFilter.includes(name.properties.name1));
				names = withinPolygon(names, boundary);
				names = names.map(name => name.properties.name1);
				let arr = [];
				names.forEach(d => {
					if (!arr.includes(d)) { arr.push(d) };
				});
				options = arr;
			}
			
			game.options = shuffle(options);
		}
	}

	// Update game.place to match game.code, if there is a mismatch
	function newCode() {
		if ((game.code && !game.place) || game.code != game.place.code ) {
			game.place = lookup[game.code];
			game.order = byDistance(game.code);
			if (!game.started) { game.started = true };
			doTurn();
		}
	}

	// Reactive code to trigger above function (usually due to a click on the map)
	$: game.code && newCode();
	
	// INITIALISE THE APP
	// Get geography codes and names from API
	getData(centroids)
	.then(result => {
		let indexed = {};

		result.forEach(d => {
			let obj = {...d, names: []};
			indexed[d.code] = obj;
		});

		return indexed;
	})
	.then(indexed => {
		let str = Date.now(); // Timestring, to avoid URL caching
		getData(baseurl + 'names.csv?str=' + str)
		.then(data => {
			let named = [];
			data.forEach(d => {
				indexed[d.code].names.push(d.name);
				if (!named.includes(d.code)) { named.push(d.code) };
			});
			lookup = indexed;
			game.named = named;

			// Load history from previous session, if it exists
			if (getState('history')) {
				game.history = getState('history');
			}
		});
	});
	
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	main {
		padding: 20px;
	}
	h1 {
		margin-top: 10px;
	}
	.block {
		display: block;
		width: 100%;
		text-align: left;
	}
	.inline {
		display: inline-block;
		width: 60%;
	}
	.submit {
		display: inline-block;
		width: 20%;
	}
	.em {
	padding: 1px 4px 1px 4px;
	white-space: nowrap;
}
</style>

<Panel>
	<main>
		{#if !game.started}<h1>Name the LSOA!</h1>{/if}
		{#if game.place && !game.message}<h2>What's a good name for {game.place.name}?</h2>{/if}
		{#if game.history[0]}<p><strong>You have named <span class="em" style="background-color: rgba(255,192,0,0.4)">{game.history.length} LSOAs</span> (of <span class="em" style="background-color: rgba(128,128,128,0.4)">{game.named.length} total</span>)</strong></p>{/if}
		{#if !game.started && lookup}
		<p>Help us to name statistical neighbourhoods (LSOAs) in England and Wales.</p>
		<p>To get started, either enter a postcode, select "random" place, or zoom in on the map and select a neighbourhood...</p>
		<form on:submit|preventDefault={() => getPostcode(game.postcode)}><input bind:value={game.postcode} name="postcode" type="text" placeholder="Enter a postcode..." class="inline" /><button type="submit" class="submit">Find!</button><button class="submit" on:click={startGame}>Random!</button></form>
		{:else if game.options}
		<p>Select a name from below or make a suggestion.</p>
		{#each game.options.slice(0, 5) as name}
	  	<button class="block" on:click={() => addName(game.code, name)}>{name}</button>
		{/each}
		<form on:submit|preventDefault={() => addName(game.code, game.entry)}>
			<input bind:value={game.entry} name="other" type="text" placeholder="Suggest a name..." class="inline" style="width: 80%" /><button type="submit" class="submit">Submit</button>
		</form>
		{:else if game.message}
			<h3>{game.message}</h3>
			<button on:click={nextPlace}>Next please!</button><br/>
			<button on:click={() => game.started = false}>I'm done.</button>
		{/if}
	</main>
</Panel>

<Map style={mapstyle} bind:map={map} maxzoom={17}>
		<MapSource
			id="lsoa-bounds"
			type="vector"
			url={lsoabounds.url}
			layer={lsoabounds.layer}
			promoteId={lsoabounds.code}
			minzoom={9}
			maxzoom={12}>
			<MapLayer
				id="lsoa-fill"
				source="lsoa-bounds"
				sourceLayer={lsoabounds.layer}
				type="fill"
				paint={{
					'fill-color': ['case',
						['==', ['feature-state', 'selected'], true], 'rgba(255, 255, 255, 0)',
						['==', ['feature-state', 'history'], true], 'rgba(255, 192, 0, 0.4)',
						['==', ['feature-state', 'named'], true], 'rgba(128, 128, 128, 0.4)',
						'rgba(255, 255, 255, 0)'
					],
				}}
				hover={true}
				bind:hovered={game.hovered}
				click={true}
				bind:selected={game.code}
				highlight={true}
				named={game.named}
				history={game.history} />
			<MapLayer
				id="lsoa-bounds"
				source="lsoa-bounds"
				sourceLayer={lsoabounds.layer}
				type="line"
				paint={{
					'line-color': ['case',
						['==', ['feature-state', 'selected'], true], 'rgba(0, 0, 0, 1)',
						['==', ['feature-state', 'hovered'], true], 'rgba(0, 0, 0, 1)',
						'rgba(0, 0, 0, 0.1)'
					],
					'line-width': ['case',
						['==', ['feature-state', 'selected'], true], 2,
						['==', ['feature-state', 'hovered'], true], 2,
						1
					]
				}} />
		</MapSource>
</Map>